.button {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 56px;

  border: 2px solid $color-blue;
  border-radius: 8px;

  font-weight: bold;
  font-size: 16px;
  line-height: 14px;
  text-decoration: none;
  color: $color-blue;

  cursor: pointer;
  transition: color $transition-dur,
    border-color $transition-dur,
    background-color $transition-dur,
    box-shadow $transition-dur;

  &--primary {
    color: #fff;
    border-color: #005ecc;
    background-color: #005ecc;
  }

  &:hover {
    color: #fff;
    border-color: #005ecc;
    background-color: #005ecc;
    box-shadow: 0 13px 27px rgba(0, 83, 181, 0.46);
  }

  &:active {
    border-color: #005ecc;
    background-color: #005ecc;
    box-shadow: 0 8px 14px rgba(0, 83, 181, 0.48);
  }
}
